.smooth-notification {
	visibility: hidden;
	-webkit-transition: .15s ease-out margin;
	transition: .15s ease-out margin;
	opacity: .1
}
.smooth-notification.down {
	margin-top: -10px;
	margin-bottom: 10px
}
.smooth-notification.up {
	margin-top: 10px;
	margin-bottom: -10px
}
.smooth-notification.left {
	margin-left: 10px;
	margin-right: -10px
}
.smooth-notification.right {
	margin-left: -10px;
	margin-right: 10px
}
div.smooth-notification.show {
	visibility: visible;
	opacity: 1;
	margin: 0
}
.smooth-notification a.notification {
	font-size: inherit;
	color: inherit;
	text-decoration: none
}
.smooth-notification.window {
	/*background: #F7F5F5 url(../img/bg.gif) repeat;*/
	background-clip: padding-box;
	border: 6px solid rgba(0, 0, 0, .14);
	border-radius: 10px;
	padding: 20px;
	min-height: 150px;
	min-width: 300px
}
.smooth-notification.yellow {
	padding: 16px 55px;
	background: #FEF9D2;
	background: -webkit-gradient( linear, left top, left bottom, from(#FEF9D2), to(#FFFCEB));
	background: linear-gradient( #FEF9D2, #FFFCEB);
	background: -moz-linear-gradient( #FEF9D2, #FFFCEB);
	-webkit-box-shadow: inset 0 1px #fff, 0 1px 2px rgba(34, 25, 25, .4);
	        box-shadow: inset 0 1px #fff, 0 1px 2px rgba(34, 25, 25, .4);
	color: #000;
	font-size: 20px
}
.smooth-notification.normal {
	padding: 8px 35px;
	background: rgba(0, 0, 0, .6);
	-webkit-box-shadow: 0 1px 2px rgba(34, 25, 25, .4);
	        box-shadow: 0 1px 2px rgba(34, 25, 25, .4);
	border-radius: 0 0 5px 5px;
	color: #fff;
	font-size: 15px
}
.fast-transition {
	-webkit-transition: .15s ease-out all;
	transition: .15s ease-out all
}
.slow-transition {
	-webkit-transition: .5s ease-out all;
	transition: .5s ease-out all
}
